import React from 'react';

const UpvotesSection = ({upvotes, articleChangeInfo, name}) => {

    const sendUpvote = async () => {
        const results = await fetch(`/api/articles/${name}/upvote`, { method: 'POST' });
        const body = await results.json();
        articleChangeInfo(body);

    };
    return (
        <div>
            <p>this articles was upvoted {upvotes} </p>
            <button onClick={() => sendUpvote()}>Up vote</button>
        </div>
    );
};


export default UpvotesSection;