import React, { useState, useEffect } from 'react'
import ArticleContent from './articles-content'
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentSection from '../components/AddCommentSection'
import ArticlesList from '../components/ArticlesList';
import NotFoundPage from './NotFoundPage';


const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = ArticleContent.find(article => article.name === name);
    const [ArticleInfo, ChangeInfo] = useState({ name: 'some name', upvotes: 3, comments: [] });
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            console.log(body);
            ChangeInfo(body);
        };
        fetchData();
    }, [name])
    if (!article) return <NotFoundPage />
    return (
        <>
            <h1>{ArticleInfo.name}</h1>
            <UpvotesSection name={name} upvotes={ArticleInfo.upvotes} articleChangeInfo={ChangeInfo}></UpvotesSection>
            {article.content.map((paragraph, key) => (

                <p key={key}>{paragraph}</p>

            ))}
            <CommentsList comments={ArticleInfo.comments} ></CommentsList>
            <AddCommentSection articleName={name} UpdateArticleInfo={ChangeInfo} />
            <h3>Other Articles</h3>
            <ArticlesList articles={ArticleContent.filter(article => (
                article.name !== name
            ))} />
        </>
    );
}

export default ArticlePage;