import React,{useState} from 'react';

const AddCommentSection = ({UpdateArticleInfo , articleName}) => {

    const [name,setName] = useState('');
    const [comment,setComment] = useState('');

    const SendAddComment = async ()=>{
        const result = await fetch(`/api/articles/${articleName}/add-comment`,{
            method:'post',
            body:JSON.stringify({username:name,text:comment}),
            headers:{
                'Content-Type' : 'application/json'
            }
        });
        const body = await result.json();

        UpdateArticleInfo(body);
        setName('');
        setComment('');

    };
    return (
        <div id="add-comment-form">
            <h3>add Comment</h3>
            <lable>
                Name
                <input  onChange={(event)=>setName(event.target.value)} type="text" value={name}></input>
            </lable>
            <lable>
                Comment
    <textarea onChange={(event)=>setComment(event.target.value)} rows="5" cols="10" value={comment}></textarea>
            </lable>
            <button onClick={()=>SendAddComment()}>Add Comment</button>
        </div>
    )
};


export default AddCommentSection;