import React from 'react'

const CommentsList = ({ comments }) => (
    <>
        <h2>Comments</h2>
        {comments.map((comment, key) => (
            <div key={key} className="comments">
                <h3>{comment.username}</h3>
                <p>{comment.text}</p>
            </div>


        ))}

    </>
);

export default CommentsList;